import { Component, OnInit } from '@angular/core';
import { INgxChatUiMessagePartner, INgxChatUiMessage, INgxChatUiState, INgxChatUiMessageType, INgxChatUiMessagePayload } from 'ngx-chat-ui';
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {
  partners: INgxChatUiMessagePartner[] = [];
  messages: {
    [chatKey: string]: INgxChatUiMessage[]
  } = {
    demoSelect: [],
    demoText: [],
    demoList: [],
    demoAutocomplete: [],
    demoUpload: []
  };
  state: {
    [chatKey: string]: INgxChatUiState
  } = {
    demoSelect: {},
    demoText: {},
    demoList: {},
    demoAutocomplete: {},
    demoUpload: {}
  };
  callbacks: {
    // tslint:disable-next-line:ban-types
    [chatKey: string]: { [name: string]: Function }
  } = {
    demoAutocomplete: {
      autocomplete: this.autocompleteTags.bind(this),
    },
    demoUpload: {
      upload: this.uploadFiles.bind(this),
    }
  };

  // tslint:disable-next-line:use-life-cycle-interface
  ngOnInit() {
    this.partners = this.partners.concat([
      {
        messagePartnerId: 'me',
        firstName: 'Sergey',
        lastName: 'Kalaus'
      },
      {
        messagePartnerId: 'it',
        // tslint:disable-next-line:max-line-length
        avatar: 'data:image/jpeg;base64,'
      }
    ]);

    this.messages.demoSelect = this.messages.demoSelect.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Hello'
        }
      },
      {
        date: new Date(),
        messageId: '1',
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.select,
          items: [
            {
              icon: '👌',
              id: 0,
              className: 'ngx-chat-ui-action-select-item-cta'
            },
            {
              text: 'No',
              id: 1
            }
          ]
        }
      },
    ]);

    this.messages.demoText = this.messages.demoText.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Hello'
        }
      },
      {
        date: new Date(),
        messageId: '2',
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.text
        }
      },
    ]);

    this.messages.demoList = this.messages.demoList.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Hello'
        }
      },
      {
        date: new Date(),
        messageId: '2',
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.select,
          className: 'ngx-chat-ui-action-select-list',
          items: [
            {
              text: 'Yes',
              id: 0,
              className: 'ngx-chat-ui-action-select-item-cta'
            },
            {
              text: 'No',
              id: 1
            }
          ]
        }
      },
    ]);

    this.messages.demoAutocomplete = this.messages.demoAutocomplete.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Hello'
        }
      },
      {
        date: new Date(),
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.autocomplete,
          callback: 'autocomplete',
        }
      },
    ]);

    this.messages.demoUpload = this.messages.demoUpload.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Hello'
        }
      },
      {
        date: new Date(),
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          value: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.upload,
          callback: 'upload',
        }
      },
    ]);
  }

  onResponse(chatKey: string, payload: INgxChatUiMessagePayload) {
    setTimeout(() => {
      this.messages[chatKey] = this.messages[chatKey].concat([
        {
          date: new Date(),
          messagePartnerId: 'me',
          payload
        }
      ]);
    });
  }

  // tslint:disable-next-line:ban-types
  autocompleteTags(query: string, callback: Function) {
    callback([
      '#aabbccdd',
      '#eeffgghh',
      '#iijjkkll',
      '#mmnnoopp',
      '#qqrrsstt',
      '#uuvvwwxx',
      '#yyzz',
    ]);
  }

  // tslint:disable-next-line:ban-types
  uploadFiles(data: any, callback: Function) {
    callback({
      name: 'Test.jpg',
      url: 'https://via.placeholder.com/150',
    });
  }

}
